@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
.card {
    background: white;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    padding: 5px;
  }

  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:#6CADDC;
  }
  .card > .img-container > img {
    width: 100%;
    height: 100%;
  }

  .card:hover {
    cursor: pointer;
  }



.wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 50px;
}
.btn {
    float: right;
    margin-right: 10px;
}

.jumbotron {
    background-color: #65771E;
    background-image: url(/clicky-game-deploy/static/media/bojack-header.afb00083.jpeg);
    background-size: 100%;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  font-family: 'Caveat', cursive;
  background-image: url(/clicky-game-deploy/static/media/husk.9001eb9c.png)
}

.container {
  display: flex;
  flex-direction: column;
}

